body {
  margin: 0;
  /* -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale; */
  font-family:Verdana, Geneva, Tahoma, sans-serif;

  background-color:#414b4d;
  color: black;
}
/*#869fc5*/
