nav {
  position: fixed;
  top: 0;
  right: 0;
  left: 0;
  height: 36px;
  z-index: 100;
  gap: 10px;
}

nav ul{
  margin: 0 auto;
  padding: 4px;
  color: black;
  list-style: none;
  display: flex;
  justify-content: space-evenly;
  background-color: whitesmoke;
}

nav ul li a {
  color: black;
  text-decoration: none;
}

.App {
  /* margin-top: 70px; */
}


footer {
  bottom: 0;
  height: 80px;
  background-color: rgb(20, 20, 20);
  width: 100%;
  display: flex;
  align-items: center;
}

.footerContainer {
  display: flex;
  margin: 0 auto;
  height: 40%;
  width: 80%;
}

.smolImg1 {
  /* margin: 0 10px; */
  height: 100%;
  width: 15%;
}

.smolImg2 {
  /* margin: 0 10px; */
  height: 100%;
  width: 4%;
}

.spacer {
  width: 60%;
  height: 50px;
}

.SectionTemplate {
  display: flex;
  flex-direction: column;
  align-items: center;
  margin: 0 auto;
  height: stretch;
  width: 60%;
  background-color: aliceblue;
  text-align: center;
  padding-bottom: 5px;
}


.ImgContainer {
  display: flex;
  height: 120px;
  width: 110px;
  display:block; 
  -webkit-transition-property: all; 
  -webkit-transition-duration: 0.3s; 
  -webkit-transition-timing-function: ease; 
  /* margin: 2px 0px; */
}

.ImgContainer:hover {
  transform: scale(1.25);
}

.ImgFill {
  width: 100%;
  height: 100%;
  border-radius: 25%;
}

.SubSection {
  margin: 5px;
}

.SubHeader {
  padding: 2px;
}

.greetings {
  text-align: center;
}

.MainImage {
  position: relative;
  height: 100vh;
}

.content {
  display: flex; 
  align-items: center;
  justify-content: center;
  flex-direction: column;
  position: absolute;
  height: 100vh; 
  width: 100%;
  color: black;
}
